import React from 'react'

import './ListItem.css';

export default (props) => {
    return (
        <div className="listItem">
             <p >{props.item}</p>
            
            <div>
                <button className="edit" onClick={props.edit}>✍</button>
                <button className="done" onClick={props.delete}>✓</button>
                <button className="delete" onClick={props.delete}>X</button>
            </div>
        </div>
    );
}



